/*Contact Form*/

.block-webform-client-block-19 {
  background-color: #e4f4ed;
  color: #004B74;
  padding: 10px;
  input {
    padding: 0.325em
  }
  .form-item label {
    margin: 0 0 0.325em;
  }
}

.block-webform-client-block-19 input.form-submit {
  width: 100%;
  text-aligh: center;
  margin-bottom: 0rem;
  font-size: 1rem;
  line-height: 1.6rem;
}

.block-webform-client-block-19 h2 {
  margin: 0px;
  line-height: 1;
  margin-bottom: 10px;
}

.form-item {
  margin-bottom: .2rem;
  font-size: .75rem;
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #bdbebf;
  opacity: 1; /* Firefox */
}

#edit-submitted-apartment-size,
#edit-submitted-which-building {
  display: flex;
  flex-wrap: wrap;
}


#edit-submitted-apartment-size .form-item {
  flex-basis: auto;
  flex-grow: 1;
}
#edit-submitted-which-building .form-item {
  width: 50%;
}

.webform-component--apartment-size,
.webform-component--which-building {
  margin-bottom: .4rem;
}

.form-item .form-item {
  margin: 0px;
  padding: 0px;
  height: 18px;
}

.form-item input.form-radio, .form-item input.form-checkbox {
  vertical-align: inherit;
  margin-right: 0.3125em;
}